import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Form, Input, Button, message } from 'antd'
import patterns from '../../constants/patterns'
import fetchAPI from '../../fetch'
import apis from '../../api'
import '../Signup/Signup.less';
const FORM_LAYOUT = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 }
}


class Verify extends Component {
  static propTypes = {
    form: PropTypes.object,
    goNext: PropTypes.func,
  }
  handleVerify = () => {
    this.props.form.validateFields(async (err, vals) => {
      if (err || (!vals.user_phone && !vals.user_email) || !vals.user_name) {
        message.error('')
        return
      }
      try {
        const result = await fetchAPI(apis.check, vals)
        if (result) {
          message.success('')
          this.props.goNext(result.id)
        } else {
          message.error('')
        }
      } catch (error) {
        console.log(error)
      }
    })
  }
  render() {
    const { getFieldDecorator } = this.props.form
    return (
      <Form layout='horizontal'>
        <Form.Item label='' {...FORM_LAYOUT}>
          {
            getFieldDecorator('user_name', {
              rules: [{ required: true, message: '' }]
            })(
              <Input placeholder='' />
            )
          }
        </Form.Item>
        <Form.Item label='' {...FORM_LAYOUT}>
          {
            getFieldDecorator('user_phone', {
              rules: [{ pattern: patterns.phone, message: '' }]
            })(
              <Input placeholder='' />
            )
          }
        </Form.Item>
        <Form.Item label='' {...FORM_LAYOUT}>
          {
            getFieldDecorator('user_email', {
              rules: [{ pattern: patterns.email, message: '' }]
            })(
              <Input placeholder='' />
            )
          }
        </Form.Item>
        <div className='signup-btn-group'>
          <Button type='primary' onClick={this.handleVerify}></Button>
          <Button onClick={() => this.props.router.replace('/login')}></Button>
        </div>
      </Form>
    )
  }
}
export default Form.create()(Verify)