import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Upload, Button } from 'antd';
import fetchAPI from '../../fetch';
import api from '../../api';

export default class ArticleUpload extends Component {
  static propTypes = {
    article: PropTypes.object
  }
  state = {
    token: '',
    canUpload: false,
  }
  componentDidMount() {
    this.fetchToken()
  }
  fetchToken = async () => {
    try {
      const token = await fetchAPI(api.getUploadToken)
      console.log(token);
      this.setState({ token })
    } catch (error) {
      console.log(error)
    }
  }
  handleChange = ({ file, fileList }) => {
    const hash = (file && file.response && file.response.hash) || ''
    const url = `http://pq5i8yb7b.bkt.clouddn.com/${hash}`
    this.setState({ canUpload: !!fileList.length })
    this.props.onChange && this.props.onChange(fileList.length
      ? { article_url: url, file_name: file.name }
      : undefined
    )
  }
  render() {
    const { token, canUpload } = this.state
    return (
      <Upload
        accept='.pdf'
        action='http://upload-z1.qiniup.com'
        data={{ upload_token: token, token, action: 'http://upload-z1.qiniup.com' }}
        onChange={this.handleChange}
        disabled={canUpload}
      >
        <Button disabled={canUpload} icon='upload'></Button>
      </Upload>
    )
  }
}
