import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Card, Form, Button, message } from 'antd';
import { FORM_CONF, FORM_LAYOUT } from './conf';
import fetchAPI from '../../fetch'
import apis from '../../api'
import './Signup.less';

class Signup extends Component {
  static propTypes = {
    router: PropTypes.object,
    form: PropTypes.object,
  }
  handleSubmit = () => {
    this.props.form.validateFields(async (err, values) => {
      if (err) {
        message.error('')
        return
      }
      if (values.user_pwd !== values.user_pwd_again) {
        message.error('')
        return
      }
      try {
        delete values.user_pwd_again
        await fetchAPI(apis.signup, values)
        message.success('')
        this.props.router.replace('/login')
      } catch (error) {
        console.log(error)
      }
    })
  }
  render() {
    const { getFieldDecorator } = this.props.form
    return (
      <Card className='signup'>
        <div className='signup-title'></div>
        <Form layout='horizontal'>
          {
            FORM_CONF.map(item => <Form.Item {...FORM_LAYOUT} key={item.key} label={item.label}>
                {getFieldDecorator(item.key, { rules: item.rules })(item.component)}
              </Form.Item>)
          }
          <div className='signup-btn-group'>
            <Button type='primary' onClick={this.handleSubmit}></Button>
            <Button onClick={() => this.props.router.replace('/login')}></Button>
          </div>
        </Form>
      </Card>
    )
  }
}

export default Form.create()(Signup)
