import React, { Component } from 'react'
import { Layout, LocaleProvider, message } from 'antd'
import PropTypes from 'prop-types'
import fetchAPI from '../../fetch';
import apis from '../../api'
// import HeaderCustom from '../Header'

import './CoreLayout.less'
import 'ant-design-pro/dist/ant-design-pro.css'
import 'antd/dist/antd.less'

import 'moment/locale/zh-cn'
import locale from 'antd/lib/locale-provider/zh_CN'
import CommonMenu from '../../components/CommonMenu';

const { Content } = Layout

// const user = {
//   name: 'zxj',
//   id: '2150165',
//   mail: 'employeeMail',
// }
const specialPaths = ['signup', 'forget']

class CoreLayout extends Component {
  constructor (props) {
    super(props)
    this.state = {
      collapsed: false,
      reRenderHash: null,
      user: {}
    }
  }
  componentDidMount() {
    if (this.props.location.pathname === '/') {
      this.props.router.replace('search')
    }
    this.fetchUserInfo()
  }
  componentWillReceiveProps(nextProps) {
    console.log(nextProps.location.pathname);
    if (!specialPaths.includes(nextProps.location.pathname)) {
      this.fetchUserInfo()
    }
  }
  fetchUserInfo = async () => {
    try {
      const user = await fetchAPI(apis.getUser)
      this.setState({ user: user || {} })
      if (!user && this.props.location.pathname !== 'login') {
        message.error('')
        this.props.router.replace('/login')
      }
    } catch (error) {
      console.log(error)
    }
  }
  routerPush = url => {
    const { router } = this.props
    router.push({
      pathname: url,
    })
  }

  get backgroundImg () {
    return <div className='pmt-back-img' />
  }

  render () {
    const {
      router,
      location,
      children,
    } = this.props
    return (
      <LocaleProvider locale={locale}>
        <Layout className='ant-layout-has-sider'>

          <CommonMenu router={router} user={this.state.user} path={location.pathname} />
            <Content
              className='custom-content'
            >
              <div className='ant-layout-container'>
                {!children ? this.backgroundImg : React.cloneElement(children, { user: this.state.user })}
              </div>
            </Content>
        </Layout>
      </LocaleProvider>
    )
  }
}

CoreLayout.contextTypes = {
  router: PropTypes.object.isRequired,
}

CoreLayout.defaultPropTypes = {
  children: <div></div>,
}

CoreLayout.propTypes = {
  location: PropTypes.object.isRequired,
  router: PropTypes.object.isRequired,
  children: PropTypes.element,
}

export default CoreLayout
