import React, { Component } from 'react'
import PropTypes from 'prop-types'
import PageHeader from 'ant-design-pro/lib/PageHeader';
import InfoBlock from '../../components/InfoBlock'
import InfoConf from './conf'
import { Card, Table, Button, message } from 'antd';
import { COLUMNS } from '../Search/conf';
import ArticleDetail from '../Search/ArticleDetail';

import '../Search/Search.less'
import UserInfoModal from '../../components/UserInfoModal';
import fetchAPI from '../../fetch';
import api from '../../api';

export default class Self extends Component {
  static propTypes = {
    router: PropTypes.object,
    user: PropTypes.object,
  }
  state = {
    visible: false,
    data: [],
    articleId: undefined,
    detailVisible: false,
    loading: false
  }
  get columns () {
    const operation = {
      title: '',
      key: 'operation',
      dataIndex: 'id',
      width: 100,
      render: (text, record) => {
        return <span>
          <span className='logout' onClick={() => {
            this.setState({ articleId: text, detailVisible: true })
          }}></span>
          <span className='logout-danger' onClick={() => this.handleUnfocus(text)}></span>
        </span>
      }
    }
    return [...COLUMNS, operation]
  }
  componentDidMount() {
    this.fetchFocusArticles()
  }
  handleUnfocus = async id => {
    try {
      await fetchAPI(api.unfocusArticle, { id })
      message.success('')
      this.fetchFocusArticles()
    } catch (error) {
      console.log(error);
    }
  }
  fetchFocusArticles = async () => {
    try {
      this.setState({ loading: true })
      const list = await fetchAPI(api.getFocusList)
      this.setState({ data: list, loading: false })
    } catch (error) {
      this.setState({ loading: false })
      console.log(error);
    }
  }
  render() {
    const { data, visible, detailVisible, articleId, loading } = this.state
    const { user } = this.props
    return (
      <div className='search'>
        <PageHeader
          title={<div></div>}
          content={<InfoBlock config={InfoConf} infoData={user} />}
          action={<Button icon='edit' type='primary' onClick={() => this.setState({ visible: true })}></Button>}
        />
        <Card title=''>
          <Table loading={loading} rowKey='id' columns={this.columns} dataSource={data}  />
        </Card>
        <ArticleDetail id={articleId} visible={detailVisible} handleClose={() => this.setState({ detailVisible: false })} />
        <UserInfoModal afterSubmit={() => window.location.reload()} visible={visible} user={user} onCancel={() => this.setState({ visible: false })} />
      </div>
    )
  }
}
