import React from 'react'
import PropTypes from 'prop-types'
import Trend from 'ant-design-pro/lib/Trend'
import numeral from 'numeral'
import _ from 'lodash'

class NumberInfo extends React.Component {
  render() {
    const { infoData, trend, color, data, dataKey, formatRule, percentage, addonAfter } = this.props
    const colorConf = { style: { color: color || '' } }
    const display = (child) => trend
      ? (<Trend {...colorConf} flag={trend} >{child}</Trend>)
      : (<span {...colorConf}>{child}</span>)
    return display(
      <React.Fragment>
       {numeral(data || _.get(infoData, dataKey) || 0).format(formatRule)}
       {addonAfter || percentage && '%'}
      </React.Fragment>
    )
  }
}

NumberInfo.propTypes = {
  // dataKey
  infoData: PropTypes.object,

  // key
  dataKey: PropTypes.string,

  // 
  data: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),

  // 
  color: PropTypes.string,

  // 
  trend: PropTypes.oneOf(['up', 'down']),

  // 
  formatRule: PropTypes.string,

  // 
  percentage: PropTypes.bool,

  // 
  addonAfter: PropTypes.string
}

NumberInfo.defaultProps = {
  formatRule: '0,0.00',
  percentage: false
}

export default NumberInfo
