import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Modal, Form, message } from 'antd';
import { FORM_CONF, FORM_LAYOUT } from './conf';
import fetchAPI from '../../fetch';
import apis from '../../api';

class UserInfoModal extends Component {
  static propTypes = {
    visible: PropTypes.bool,
    user: PropTypes.object,
    form: PropTypes.object,
    onCancel: PropTypes.func,
    isAdmin: PropTypes.bool,
    afterSubmit: PropTypes.func,
  }
  handleSubmit = () => {
    this.props.form.validateFields(async (err, vals) => {
      if (err) {
        message.error('')
        return
      }
      try {
        await fetchAPI(apis.changeUser, { ...vals, id: this.props.user.id })
        message.success('')
        this.props.onCancel()
        this.props.afterSubmit && this.props.afterSubmit()
      } catch (error) {
        console.log(error)
      }
    })
  }
  render() {
    const { visible, form, onCancel, user={}, isAdmin=false } = this.props
    return (
      <Modal title='' visible={visible} onOk={this.handleSubmit} onCancel={onCancel} destroyOnClose>
        <Form layout='horizontal'>
          {
            FORM_CONF(isAdmin).map(item => <Form.Item {...FORM_LAYOUT} key={item.key} label={item.label}>
                {form.getFieldDecorator(item.key, { rules: item.rules, initialValue: user[item.key] })(item.component)}
              </Form.Item>)
          }
        </Form>
      </Modal>
    )
  }
}

export default Form.create()(UserInfoModal)
