import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Modal, Form, message } from 'antd';
import { FORM_CONF, FORM_LAYOUT } from './conf'
import fetchAPI from '../../fetch';
import api from '../../api';

class ArticleModal extends Component {
  static propTypes = {
    isEdit: PropTypes.bool,
    form: PropTypes.object,
    visible: PropTypes.bool,
    handleCancel: PropTypes.func,
    afterCreate: PropTypes.func,
  }
  handleCreate = () => {
    this.props.form.validateFields(async (err, vals) => {
      if (err) {
        message.error('')
        return
      }
      const params = { ...vals, ...vals.article_file }
      delete params.article_file
      try {
        await fetchAPI(api.createArticle, params)
        message.success('')
        this.props.handleCancel()
        this.props.afterCreate()
      } catch (error) {
        console.log(error)
      }
    })
  }
  handleEdit = () => {
    this.props.form.validateFields(async (err, vals) => {
      if (err) {
        message.error('')
        return
      }
      const params = { ...this.props.article, ...vals }
      delete params.created_at
      delete params.updated_at
      try {
        await fetchAPI(api.updateArticle, params)
        message.success('')
        this.props.handleCancel()
        this.props.afterCreate()
      } catch (error) {
        console.log(error)
      }
    })
  }
  render() {
    const { isEdit, form: { getFieldDecorator }, article, visible } = this.props
    return (
      <Modal visible={visible} title={`${isEdit ? '' : ''}`} destroyOnClose onOk={isEdit ? this.handleEdit : this.handleCreate} onCancel={this.props.handleCancel}>
        <Form layout='horizontal'>
          {
            FORM_CONF(isEdit, article).map(item => !item.cannotshow && <Form.Item {...FORM_LAYOUT} key={item.key} label={item.label}>
                {getFieldDecorator(item.key, { rules: item.rules, initialValue: item.initialValue })(item.component)}
              </Form.Item>)
          }
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(ArticleModal)