import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { COLUMNS } from '../Search/conf'
import { Card, Table, Button, Popconfirm, message } from 'antd';
import '../Search/Search.less';
import TagList from './TagList';
import './ArticleManager.less'
import ArticleModal from './ArticleModal';
import fetchAPI from '../../fetch';
import api from '../../api';

export default class ArticleManager extends Component {
  static propTypes = {
    prop: PropTypes
  }
  get columns () {
    return [...COLUMNS, {
      title: '',
      key: 'operation',
      dataIndex: 'id',
      width: 100,
      render: (text, record) => {
        return <span>
          <span className='logout' onClick={() => {
            this.setState({ visible: true, isEdit: true, article: record })
          }}></span>
          <Popconfirm onConfirm={() => this.handleDelete(text)} title=''>
            <span className='logout-danger'></span>
          </Popconfirm>
        </span>
      }
    }]
  }
  state = {
    data: [],
    isEdit: false,
    visible: false,
    article: {},
    loading: false
  }
  componentDidMount() {
    this.fetchArticles()
  }
  handleCreate = () => {
    this.setState({ visible: true, isEdit: false, article: {} })
  }
  handleDelete = async id => {
    try {
      await fetchAPI(api.deleteArticle, { id })
      message.success('')
      this.fetchArticles()
    } catch (error) {
      console.log(error);
    }
  }
  fetchArticles = async () => {
    try {
      this.setState({ loading: true })
      const list = await fetchAPI(api.getArticles)
      this.setState({ loading: false, data: list })
    } catch (error) {
      this.setState({ loading: false })
    }
  }
  render() {
    const { visible, isEdit, article, loading } = this.state
    return (
      <div className='search article-manager'>
        <Card title=''>
          <TagList />
        </Card>
        <Card title='' extra={<Button type='primary' onClick={this.handleCreate}></Button>}>
          <Table loading={loading} rowKey='id' columns={this.columns} dataSource={this.state.data} />
        </Card>
        <ArticleModal visible={visible} isEdit={isEdit} article={article} afterCreate={this.fetchArticles} handleCancel={() => { this.setState({visible: false }) }} />
      </div>
    )
  }
}
