import fetch from 'isomorphic-fetch'
import { message as ms } from 'antd'
const SUCCESS = 1
/**
 * 
 *
 * @param {Object} response fetch
 * @return {Object}  reject 
 */
function checkStatus (response) {
  if (response.status >= 200 && response.status < 300) {
    return Promise.resolve(response)
  } else {
    const message = getErrorMsgByStatusCode(response.msg, response.status)
    return Promise.reject({ message, response })
  }
}
function checkCode (response) { 
  if (response.code === SUCCESS) {
    return Promise.resolve(response.data) 
  } else { 
    return Promise.reject(response) 
  } 
} 
  
  
/**
 * 
 *
 * @param {number} code 
 * @return {string} 
 */
function getErrorMsgByStatusCode (msg, code) {
  let result = ''
  if (code >= 400 && code < 500) {
    switch (code) {
      case 401:
        result = msg || ',.'
        break
      case 403:
        result = msg || '.'
        break
      case 404:
        result = msg || '.'
        break
      case 405:
        result = msg || '.'
        break
      case 406:
        result = msg || '.'
        break
      default:
        result = `(${code}).`
    }
  } else if (code >= 500 && code < 600) {
    result = msg || '.'
  }
  return result
}

/**
 * 
 *
 * @param {Object} e 
 */
function handleError (e) {
  console.log('request failed', e)
  // 
  !e.response && (e.message = (navigator && navigator.onLine) ? '' : '')
  ms.error(e.msg || e.message)
  throw e
}

/**
 * 
 * @param {Object} res response
 */
function jsonParse (res) {
  return res.json()
    .then(jsonResult => jsonResult)
    .catch((err) => {
      const error = new Error(err.message)
      ms.error('json ')
      Promise.reject(error)
    })
}

export default {
  request:(url, config) => {
    return fetch(url, config)
      .then(checkStatus)
      .then(jsonParse)
      .then(checkCode)
      .catch(handleError)
  },
}
