import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { Row, Col, Tooltip, Card } from 'antd'
import NumberInfo from '../NumberInfo'
import Ellipsis from 'ant-design-pro/lib/Ellipsis'
// import {
//   renderMap
// } from '../utils'

import _ from 'lodash'

import './infoBlock.less'

const SPECIAL_NODES = [ 'table' ]

class InfoBlock extends Component {
  constructor (props) {
    super(props)
    this.state = {
      colLayout: 12,
      loading: false,
    }
  }
  componentDidMount () {
    const { loading, needResponsiveLayout } = this.props
    this.setState({ loading })
    needResponsiveLayout && (this._observer = this.getMutationObserver(this.handleMutation, 20))
  }
  componentWillReceiveProps (nextProps) {
    this.props.loading !== nextProps.loading && this.setState({ loading: nextProps.loading })
  }
  componentDidUpdate (prevProps) {
    const { infoData, containerSelector, needResponsiveLayout } = this.props
    if (!_.isEqual(prevProps.infoData, infoData)) {
      this.handleLongItem()
    }
    if (needResponsiveLayout) {
      const _dom = document.querySelector(containerSelector)
      _dom && this._observer.observe(_dom, { 'attributes': true })
    }
  }
  getColLayout = width => {
    const { xs, sm, lg, xl } = this.props.layout
    let colLayout
    if (width <= 570) {
      colLayout = xs || 12
    } else if (width <= 800) {
      colLayout = sm || xs
    } else if (width <= 1200) {
      colLayout = lg || sm || xs
    } else {
      colLayout = xl || lg || sm || xs
    }
    return colLayout
  }
  handleMutation = mutations => {
    const { colLayout } = this.state
    const currentWidth = mutations[0].target.offsetWidth
    const newColLayout = this.getColLayout(currentWidth)
    colLayout !== newColLayout && this.setState({ colLayout: newColLayout })
  }
  /**
   * MutationObserver
   * @param {Function} func Mutation
   * @param {Number} delay ms
   * @returns {Object} MutationObserver
   */
  getMutationObserver = (func, delay) => {
    // MutationObserver
    const Observer = window.MutationObserver ||
      window.WebKitMutationObserver ||
      window.MozMutationObserver
    return new Observer(_.debounce(func, delay, { 'leading': true }))
  }
  handleLongItem = () => {
    let span = document.createElement('span')
    span.style.visibility = 'hidden' // 
    span.style.display = 'inline-block'
    document.body.appendChild(span) // 
    const domArray = document.querySelectorAll('div.antx-info-block')
    _.forEach(domArray, val => {
      const labelClasses = _.get(val, 'firstChild.firstChild.firstChild.classList') || []
       // label
      const hasLabel = _.includes(labelClasses, 'antx-item-label')
      const hasCol = _.includes(labelClasses, 'antx-item-with-col')
      const hasCalculated = _.includes(labelClasses, 'antx-info-calculated')
      if (hasLabel && !hasCalculated && !hasCol) { // label, 
        val.firstChild.firstChild.firstChild.className += ' antx-info-calculated'
        span.innerHTML = val.innerHTML // 
        const singleWidth = span.offsetWidth // 
        const currentColLayout = this.getColLayout(val.offsetWidth)
        // 
        const currentWidth = currentColLayout / 24 * val.offsetWidth
        if (currentWidth < singleWidth) {
          let width = 2 * (currentColLayout / 24 * 100)
          width = width > 100 ? 100 : width
          val.style.width = width + '%'
        }
      }
    })
    span.parentNode.removeChild(span) // dom
  }
  getCurrentInfo = (conf, key, isChange) => {
    const { infoData, changeData } = this.props
    if (isChange && !Object.keys(changeData).length) {
      return
    }
    const { render, dataKey } = conf
    const renderKey = dataKey || key
    const currentData = _.get(isChange ? changeData : infoData, renderKey)
    // if (expandRender && _.isString(render)) {
    //   const [type, params] = render.split(':')
    //   return renderMap[type](params)(currentData)
    // }
    if (!currentData && isChange) {
      return
    }
    return render
      ? (_.isString(render)
        ? this.handleRender(conf, render, renderKey, infoData)
        : render(currentData, infoData))
      : (currentData || '--')
  }
  handleRender = (conf, renderType, dataKey, infoData) => {
    const renderMap = new Map([
      ['number', <NumberInfo infoData={infoData} dataKey={dataKey} {...conf.customOptions} />],
    ])
    const neededRenderComponent = renderMap.get(renderType)
    if (!neededRenderComponent) {
      throw new TypeError(`[InfoBlock Error]'${renderType}' can not be configured as 
      render: '${renderType}', you may need a render function, please check your configuration!`)
    }
    return neededRenderComponent
  }

  render () {
    const { colLayout, loading } = this.state
    const { config, layout, needResponsiveLayout, expandRender, changeData } = this.props
    return (
      <Card loading={loading} bordered={false} className='antx-info-block-card' size={60}>
        <Row>
          { Object.keys(config).map(key => {
            const { isLongItem, withoutLabel, col, hasAuth, render, label } = config[key]
            const isSpecialNode = _.includes(SPECIAL_NODES, render)
            const labelClass = col ? 'antx-item-label antx-item-with-col' : 'antx-item-label'
            //  specialNode > col > 
            const currentLayout = isSpecialNode
              ? { span: 24 }
              : col
                ? { span: col }
                : needResponsiveLayout
                  ? { span: colLayout }
                  : layout
            const renderResult = this.getCurrentInfo(config[key], key)
            const renderChangeResult = this.getCurrentInfo(config[key], key, true)
            const needRenderChangedData = !withoutLabel &&
              !isSpecialNode && renderChangeResult &&
              renderChangeResult !== '--'
            if ((!_.isUndefined(hasAuth) && !hasAuth) || _.isUndefined(renderResult)) {
              return
            }
            return (<Col
              {...currentLayout}
              style={{ minHeight: 35, lineHeight: '35px' }}
              className='antx-info-block'
              key={key}>
              {
                isLongItem && !expandRender
                  ? <Tooltip title={renderResult} autoAdjustOverflow>
                      <Ellipsis lines={1}>
                        {(!withoutLabel && !isSpecialNode) && <span className={labelClass}>{label}: </span>}
                        {renderResult}
                      </Ellipsis>
                    </Tooltip>
                  : <div className='expand-render'>
                      <div className='base-detail'>
                        {(!withoutLabel && !isSpecialNode) && 
                          <span className={labelClass}>{label}: </span>
                        }
                        <span className='expand-value'>{renderResult}</span>
                      </div>
                      {needRenderChangedData && <div className='antx-change-info'>
                          <span className='antx-item-label'>: </span>
                          <span className='expand-value'>{renderChangeResult}</span>
                        </div>}
                    </div>
              }
            </Col>)
          }) }
        </Row>
      </Card>
    )
  }
}

InfoBlock.propTypes = {
  // 
  infoData: PropTypes.object,

  // 
  config: PropTypes.object,

  // MutationObserver
  needResponsiveLayout: PropTypes.bool,

  // 
  layout: PropTypes.object,

  // 
  loading: PropTypes.bool,

  // needResponsiveLayouttrue
  containerSelector: PropTypes.string,

  //  render true  api  table render 
  expandRender: PropTypes.bool,

  // 
  changeData: PropTypes.object,
}
InfoBlock.defaultProps = {
  layout: { xs: 24, sm: 12, lg: 8, xl: 6 },
  loading: false,
  infoData: {},
  containerSelector: 'div.folding-board',
  expandRender: false,
  changeData: {},
}

export default InfoBlock
