import React from 'react';
import { Input } from "antd";
import patterns from '../../constants/patterns';

export const FORM_CONF = isAdmin => [{
  label: '',
  key: 'user_name',
  component: <Input disabled={!isAdmin} placeholder='' />,
  rules: [
    { required: true, message: '' },
    { pattern: patterns.username, message: '' }
  ]
}, {
  label: '',
  key: 'user_email',
  component: <Input placeholder='' />,
  rules: [
    { pattern: patterns.email, message: '' }
  ]
}, {
  label: '',
  key: 'user_phone',
  component: <Input placeholder='' />,
  rules: [
    { pattern: patterns.phone, message: '' }
  ]
}]

export const FORM_LAYOUT = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 }
}