import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Drawer } from 'antd'
import PageHeader from 'ant-design-pro/lib/PageHeader';
import fetchAPI from '../../fetch';
import api from '../../api';
import InfoBlock from '../../components/InfoBlock';
import { INFO_CONF } from './conf';

export default class ArticleDetail extends Component {
  static propTypes = {
    id: PropTypes.number,
    visible: PropTypes.bool,
    handleClose: PropTypes.func
  }
  state = {
    data: {},
  }
  componentWillReceiveProps(nextProps) {
    this.props.id !== nextProps.id && this.fetchDetail(nextProps.id)
  }
  
  fetchDetail = async id => {
    try {
      const data = await fetchAPI(api.getArticleDetail, { id })
      this.setState({ data })
    } catch (error) {
      console.log(error)
    }
  }
  render() {
    const { data } = this.state
    return (
      <Drawer destroyOnClose placement='left' width={1000} title='' visible={this.props.visible} onClose={this.props.handleClose}>
        <PageHeader
          title={data.article_title || '--'}
          logo={<img src='http://pq5i8yb7b.bkt.clouddn.com/page.png' alt='icon' />}
          content={<InfoBlock infoData={data} config={INFO_CONF} />}
        />
        <iframe title={data.article_title} style={{ width: '100%' }} src={data.article_url} width={1000} height={620} />
      </Drawer>
    )
  }
}
