import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Spin, Tag, message, Input, Divider, Button, Popconfirm, Empty } from 'antd';
import fetchAPI from '../../fetch';
import apis from '../../api';

export default class TagList extends Component {
  static propTypes = {
    prop: PropTypes
  }
  state = {
    loading: false,
    tags: [],
    typeName: undefined,
  }
  componentDidMount() {
    this.fetchTags()
  }
  fetchTags = async () => {
    try {
      this.setState({ loading: true })
      const result = await fetchAPI(apis.getArticleTypes)
      this.setState({ loading: false, tags: result })
    } catch (error) {
      this.setState({ loading: false })
      console.log(error)
    }
  }
  handleAdd = async () => {
    try {
      await fetchAPI(apis.addArticleType, { name: this.state.typeName })
      message.success('')
      this.setState({ typeName: undefined })
      this.fetchTags()
    } catch (error) {
      console.log(error);
    }
  }
  render() {
    const { loading, tags, typeName } = this.state
    return (
      <div className='tag-list'>
        <Spin spinning={loading}>
          <div className='tag-list-add'>
            <Input placeholder='' value={typeName} onChange={e => this.setState({ typeName: e.target.value })} />
            <Popconfirm title='' onConfirm={this.handleAdd}>
              <Button type='primary' icon='plus'></Button>
            </Popconfirm>
          </div>
          <Divider />
          <div>
            {
              tags.length ? tags.map(item => <Tag key={item.id} color='#108ee9'>{item.name}</Tag>) : <Empty />
            }
          </div>
        </Spin>
      </div>
    )
  }
}
