import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Form, Card, Row, Col, Input, Button, Table, message } from 'antd';
import { COLUMNS } from './conf';
import fetchAPI from '../../fetch';
import api from '../../api'
import './Search.less'
import ArticleTypeSelect from '../../components/ArticleTypeSelect';
import ArticleDetail from './ArticleDetail';
/* eslint-disable */
class Search extends Component {
  static propTypes = {
    form: PropTypes.object,
    router: PropTypes.object,
    user: PropTypes.object
  }
  state = {
    data: [],
    detailVisible: false
  }
  get columns () {
    const { user } = this.props
    const operation = {
      title: '',
      key: 'operation',
      dataIndex: 'id',
      width: 100,
      render: (text, record) => {
        const isFocused = record.focus_people && JSON.parse(record.focus_people).includes(String(user.id))
        return <span>
          <span className='logout' onClick={() => {
            this.setState({ articleId: text, detailVisible: true })
          }}></span>
          {!user.is_admin && (!isFocused
            ? <span className='logout' onClick={() => this.handleFocus(text, true)}></span>
            : <span className='logout-danger' onClick={() => this.handleFocus(text, false)}></span>)}
        </span>
      }
    }
    return [...COLUMNS, operation]
  }
  componentDidMount() {
    this.fetchArticles()
  }
  fetchArticles = async (params={}) => {
    try {
      this.setState({ loading: true })
      const list = await fetchAPI(api.getArticles, params)
      this.setState({ loading: false, data: list })
    } catch (error) {
      this.setState({ loading: false })
    }
  }
  handleFocus = async (id, isFocus) => {
    try {
      await fetchAPI(isFocus ? api.focusArticle : api.unfocusArticle, { id })
      message.success(`${isFocus ? '' : ''}`)
      this.fetchArticles()
    } catch (error) {
      console.log(error);
    }
  }
  handleSearch = () => {
    const params = this.props.form.getFieldsValue()
    for (const key in params) {
      if (!params[key]) {
        delete params[key]
      }
    }
    this.fetchArticles(params)
  }
  render() {
    const { getFieldDecorator, resetFields } = this.props.form
    const { data, loading, detailVisible, articleId } = this.state
    return (
      <div className='search'>
        <Card>
          <Form layout='horizontal'>
            <Row>
              <Col span={8}>
                <Form.Item label='' labelCol={{ span: 6 }} wrapperCol={{ span: 14 }}>
                  {getFieldDecorator('article_title')(
                    <Input placeholder='' />
                  )}
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label='' labelCol={{ span: 6 }} wrapperCol={{ span: 14 }}>
                  {getFieldDecorator('article_author')(
                    <Input placeholder='' />
                  )}
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label='' labelCol={{ span: 6 }} wrapperCol={{ span: 14 }}>
                  {getFieldDecorator('article_type')(
                    <ArticleTypeSelect placeholder='' />
                  )}
                </Form.Item>
              </Col>
            </Row>
          </Form>
          <div style={{ textAlign: 'right' }}>
            <Button style={{ marginRight: 10 }} icon='search' type='primary' onClick={this.handleSearch}></Button>
            <Button icon='rollback' type='danger' onClick={() => {
              resetFields()
              this.fetchArticles()
            }}></Button>
          </div>
        </Card>
        <Card>
          <Table loading={loading} rowKey='id' columns={this.columns} dataSource={data} />
        </Card>
        <ArticleDetail id={articleId} visible={detailVisible} handleClose={() => this.setState({ detailVisible: false })} />
      </div>
    )
  }
}

export default Form.create()(Search)
