import React from 'react';
import { Input } from "antd";
import ArticleTypeSelect from '../../components/ArticleTypeSelect';
import ArticleUpload from '../../components/ArticleUpload';
export const FORM_CONF = (isEdit, article={}) => [{
  label: '',
  key: 'article_title',
  component: <Input placeholder='' />,
  initialValue: article.article_title,
  rules: [
    { required: true, message: '' },
  ]
}, {
  label: '',
  key: 'article_author',
  component: <Input placeholder='/' />,
  initialValue: article.article_author,
  rules: [
    { required: true, message: '' },
  ]
}, {
  label: '',
  key: 'article_type',
  component: <ArticleTypeSelect placeholder='' />,
  initialValue: Number(article.article_type) || undefined,
  rules: [
    { required: true, message: '' },
  ]
}, {
  label: '',
  key: 'article_desc',
  component: <Input.TextArea placeholder='' />,
  initialValue: article.article_desc,
}, {
  label: '',
  key: 'article_file',
  cannotshow: isEdit,
  component: <ArticleUpload />,
  rules: [
    { required: true, message: '' }
  ]
}]

export const FORM_LAYOUT = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 }
}