import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Lg from 'ant-design-pro/lib/Login'
import { Card, message, Button, Icon } from 'antd'
import fetchAPI from '../../fetch'
import apis from '../../api'
import './Login.less'

const { UserName, Password, Tab, Submit } = Lg
/* eslint-disable */
export default class Login extends Component {
  static propTypes = {
    router: PropTypes.object
  }
  state = {
    tab: 'common',
  }
  handleSubmit = async (err, value) => {
    if (err) {
      message.error('')
      return
    }
    try {
      await fetchAPI(apis.login, { ...value, is_admin: this.state.tab === 'admin' ? 1 : 0 })
      message.success('')
      this.props.router.replace('/search')
    } catch (error) {
      console.log(error)
    }
  }
  render() {
    return (
      <Card className='login'>
        <div className='login-title'></div>
        <Lg defaultActiveKey='common' onTabChange={tab => this.setState({ tab })} onSubmit={this.handleSubmit}>
          <Tab key='common' tab='' />
          <Tab key='admin' tab='' />
          <UserName placeholder='' name='user_name' />
          <Password placeholder='' name='user_pwd' />
          <div className='login-extra'>
            <a onClick={() => this.props.router.replace('/forget')}><Icon type="question-circle" /> </a>
            <a onClick={() => this.props.router.replace('/signup')}><Icon type="user" /> </a>
          </div>
          <Submit></Submit>
        </Lg>
      </Card>
    )
  }
}
