import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Card, Steps } from 'antd';
import Verify from './Verify';
import SetPwd from './SetPwd';
import '../Login/Login.less';

export default class ForgetPwd extends Component {
  static propTypes = {
    router: PropTypes.object
  }
  state = {
    current: 0,
    id: undefined
  }
  get mainContent () {
    const ContentMap = {
      0: <Verify router={this.props.router} goNext={(id) => this.setState({ current: this.state.current + 1, id })} />,
      1: <SetPwd router={this.props.router} id={this.state.id} />
    }
    return ContentMap[this.state.current]
  }
  render() {
    const { current } = this.state
    return (
      <Card>
        <div className='login-title'></div>
        <Steps style={{ padding: '20px 20%' }} current={current}>
          <Steps.Step title='' />
          <Steps.Step title='' />
        </Steps>
        <div style={{ padding: '10px 20%' }}>
          {this.mainContent}
        </div>
      </Card>
    )
  }
}
