import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Menu, Layout, Icon } from 'antd'
import getMenu from './conf'
import Logout from '../Logout'
import './CommonMenu.less'

const  { Header } = Layout

export default class CommonMenu extends Component {
  static propTypes = {
    user: PropTypes.object,
    path: PropTypes.string,
    router: PropTypes.object
  }
  handleClickMenu = ({ keyPath }) => {
    this.props.path !== keyPath && this.props.router.replace(keyPath[0])
  }
  render() {
    const { user, path, router } = this.props
    const isLogin = !!Object.keys(user || {}).length
    return (
      <Header className='common-header'>
        <div className="logo">
          <img src="https://s2.ax1x.com/2019/04/17/AzNZvT.png" alt="AzNZvT.png" border="0" />
          <div></div>
        </div>
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={[path]}
          style={{ lineHeight: '64px' }}
        >
          {
            isLogin && getMenu(user.is_admin).map(item => <Menu.Item onClick={this.handleClickMenu} key={item.key}>
              <Icon type={item.icon} />{item.title}
            </Menu.Item>)
          }
        </Menu>
        <div className='log-info'>
          {isLogin && <Logout router={router} user={user} />}
        </div>
      </Header>
    )
  }
}
