import React, { Component } from 'react'
import PropTypes from 'prop-types'
import UserInfoModal from '../../components/UserInfoModal';
import { Card, Table, Popconfirm, message } from 'antd'
import { COLUMNS } from './conf'
import '../Search/Search.less';
import fetchAPI from '../../fetch';
import apis from '../../api';

export default class UserManager extends Component {
  static propTypes = {
    user: PropTypes.object
  }
  state = {
    data: [],
    loading: false,
    visible: false,
    user: {}
  }
  get columns () {
    return [...COLUMNS, {
      title: '',
      key: 'operation',
      dataIndex: 'id',
      width: 100,
      render: (text, record) => {
        return <span>
          <span className='logout' onClick={() => this.setState({ user: record, visible: true })}></span>
          <Popconfirm title='' onConfirm={() => this.handleDelete(text)}>
            <span className='logout-danger'></span>
          </Popconfirm>
        </span>
      }
    }]
  }
  componentDidMount() {
    this.getList()
  }
  getList = async () => {
    try {
      this.setState({ loading: true })
      const list = await fetchAPI(apis.getAllUsers)
      this.setState({ loading: false, data: list })
    } catch (error) {
      this.setState({ loading: false })
    }
  }
  handleDelete = async (id) => {
    try {
      await fetchAPI(apis.deleteUser, { id })
      message.success('')
      this.getList()
    } catch (error) {
      console.log(error);
    }
  }
  render() {
    const { data, loading, visible, user } = this.state
    return (
      <div className='search'>
        <Card title=''>
          <Table loading={loading} rowKey='id' columns={this.columns} dataSource={data || []} />
        </Card>
        <UserInfoModal afterSubmit={this.getList} visible={visible} user={user} isAdmin onCancel={() => this.setState({ visible: false })}  />
      </div>
    )
  }
}
