import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Form, Input, Button, message } from 'antd'
import patterns from '../../constants/patterns'
import fetchAPI from '../../fetch'
import apis from '../../api'
import '../Signup/Signup.less';
const FORM_LAYOUT = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 }
}


class SetPwd extends Component {
  static propTypes = {
    form: PropTypes.object,
    router: PropTypes.object,
    id: PropTypes.string
  }
  handleVerify = () => {
    this.props.form.validateFields(async (err, vals) => {
      if (err || (vals.user_pwd !== vals.user_pwd_again)) {
        message.error('')
        return
      }
      try {
        await fetchAPI(apis.changePwd, { id: this.props.id, user_pwd: vals.user_pwd })
        message.success('')
        this.props.router.replace('/login')
      } catch (error) {
        console.log(error)
      }
    })
  }
  render() {
    const { getFieldDecorator } = this.props.form
    return (
      <Form layout='horizontal'>
        <Form.Item label='' {...FORM_LAYOUT}>
          {
            getFieldDecorator('user_pwd', {
              rules: [
                { required: true, message: '' },
                { pattern: patterns.password, message: '' }
              ]
            })(
              <Input.Password placeholder='6' />
            )
          }
        </Form.Item>
        <Form.Item label='' {...FORM_LAYOUT}>
          {
            getFieldDecorator('user_pwd_again', {
              rules: [
                { required: true, message: '' },
                { pattern: patterns.password, message: '' }
              ]
            })(
              <Input.Password placeholder='' />
            )
          }
        </Form.Item>
        <div className='signup-btn-group'>
          <Button type='primary' onClick={this.handleVerify}></Button>
          <Button onClick={() => this.props.router.replace('/login')}></Button>
        </div>
      </Form>
    )
  }
}
export default Form.create()(SetPwd)