import fetch from './fetch'
import _ from 'lodash'
import querystring from 'querystring'

export default function fetchAPI (api, body, multipartFormData = false) {
  let { url, method } = api
  let config = {
    headers: {
      'Accept-Language': 'zh-CN',
      'X-Requested-With': 'XMLHttpRequest',
    },
    credentials: 'include',
    method,
  }
  if (_.isFunction(url)) {
    const { $dept } = body
    url = url($dept)
    delete body.$dept
  }
  if (body && body.$rest) {
    url = url + `/${body.$rest}`
    delete body.$rest
  }
  if (method === 'GET') {
    if (body) {
      if (!_.isEmpty(body)) {
        let params = _.isPlainObject(body) ? querystring.stringify(body) : body
        url = url + `?${params}`
      }
    }
  } else {
    if (multipartFormData) {
      const formData = new FormData()
      for (let name in body) {
        // stringify object by jican@bytedance.com
        let value = _.isPlainObject(body[name]) ? JSON.stringify(body[name]) : body[name]
        formData.append(name, value)
      }
      config.body = formData
    } else {
      config.headers['Content-Type'] = 'application/json'
      config.body = JSON.stringify(body)
    }
  }
  return fetch.request(url, config)
}
